$(document).ready(function() {
  var modal = $(".modal"),
    modalBtn = $("[data-toggle=modal]"),
    closeBtn = $(".modal__close");
  thanks = $(".thanks");
  closeBtnThanks = $(".thanks__close");

  modalBtn.on("click", function() {
    modal.toggleClass("modal--visible");
  });
  closeBtn.on("click", function() {
    modal.toggleClass("modal--visible");
  });
  modal.on("click", function(event) {
    if ($(event.target).is("modal__dialog")) event.stopPropagation();
    else if ($(event.target).is(".modal")) modal.toggleClass("modal--visible");
  });
  $(document).on("keydown", function(event) {
    if (event.keyCode === 27 && $(".modal").hasClass("modal--visible"))
      modal.toggleClass("modal--visible");
  });
  //   
  closeBtnThanks.on("click", function() {
    thanks.removeClass("thanks--visible");
  });
  thanks.on("click", function(event) {
    if ($(event.target).is("thanks__dialog")) event.stopPropagation();
    else if ($(event.target).is(".thanks"))
      thanks.removeClass("thanks--visible");
  });
  $(document).on("keydown", function(event) {
    if (event.keyCode === 27 && $(".thanks").hasClass("thanks--visible"))
      thanks.removeClass("thanks--visible");
  });

  $(document).scroll(function() {
    if ($(this).scrollTop() >= 30 && $(window).width() >= 760) {
      if ($(".button-up").is(":hidden")) {
        $(".button-up")
          .css({ opacity: 1 })
          .fadeIn("slow");
      }
    } else {
      $(".button-up")
        .stop(true, false)
        .fadeOut("fast");
    }
  });
  $(".button-up").click(function() {
    $("html, body")
      .stop()
      .animate({ scrollTop: 0 }, 700);
  });

  var mySwiper = new Swiper(".swiper-container", {
    loop: true,
    pagination: {
      el: ".swiper-pagination",
      type: "bullets"
    },
    navigation: {
      nextEl: ".swiper-button-next",
      prevEl: ".swiper-button-prev"
    }
  });

  var next = $(".swiper-button-next");
  var prev = $(".swiper-button-prev");
  var bullets = $(".swiper-pagination");
  var bullet = $(".swiper-pagination.bullet");

  var positionPrev = prev.position();

  bullets.css("left", positionPrev.left + prev.width() + 20);
  next.css(
    "left",
    positionPrev.left + prev.width() + 20 + bullets.width() + 20
  );

  new WOW().init();

  //  

  var typesCard = $(".types__card");
  var typesHeight =
    $(".header").height() +
    $(".hero").height() +
    $(".projects").height() +
    $(".control").height() -
    200;

  $(window).scroll(function() {
    if ($(this).scrollTop() >= typesHeight && $(window).width() >= 760)
      typesCard.addClass("animation-fade-up");
  });

  //  
  $(".modal__form").validate({
    errorClass: "invalid",
    errorElement: "div",
    rules: {
      //  
      userName: {
        required: true,
        minlength: 2,
        maxlength: 15
      },
      userPhone: {
        required: true,
        minlength: 17
      },
      // - ()
      userEmail: {
        required: true,
        email: true
      },
      policyCheckbox: "required"
    }, // 
    messages: {
      userName: {
        required: " ",
        minlength: "      ",
        maxlength: "     15 "
      },
      userPhone: {
        required: " ",
        minlength: "     11 "
      },
      userEmail: {
        required: " ",
        email: "  : name@domain.com"
      },
      policyCheckbox: " "
    },

    submitHandler: function(form) {
      $.ajax({
        type: "POST",
        url: "send.php",
        data: $(form).serialize(),
        success: function(response) {
          modal.removeClass("modal--visible");
          $(".thanks").addClass("thanks--visible");
          $(form)[0].reset();
        }
      });
    }
  });
  $(".footer__form").validate({
    errorClass: "invalid",
    errorElement: "div",
    rules: {
      //  
      userName: {
        required: true,
        minlength: 2,
        maxlength: 15
      },
      userPhone: {
        required: true,
        minlength: 17
      },
      // - ()
      userQuestion: {
        required: true
      },
      policyCheckbox: "required"
    }, // 
    messages: {
      userName: {
        required: " ",
        minlength: "      ",
        maxlength: "     15 "
      },
      userPhone: {
        required: " ",
        minlength: "     11 "
      },
      userQuestion: {
        required: " "
      },
      policyCheckbox: " "
    },
    submitHandler: function(form) {
      $.ajax({
        type: "POST",
        url: "send.php",
        data: $(form).serialize(),
        success: function(response) {
          $(".thanks").addClass("thanks--visible");
          $(form)[0].reset();
        }
      });
    }
  });
  $(".control__form").validate({
    errorClass: "invalid",
    errorElement: "div",
    rules: {
      //  
      userName: {
        required: true,
        minlength: 2,
        maxlength: 15
      },
      userPhone: {
        required: true,
        minlength: 17
      },
      policyCheckbox: "required"
    }, // 
    messages: {
      userName: {
        required: " ",
        minlength: "      ",
        maxlength: "     15 "
      },
      userPhone: {
        required: " ",
        minlength: "     11 "
      },
      policyCheckbox: " "
    },
    submitHandler: function(form) {
      $.ajax({
        type: "POST",
        url: "send.php",
        data: $(form).serialize(),
        success: function(response) {
          $(".thanks").addClass("thanks--visible");
          $(form)[0].reset();
        }
      });
    }
  });

  //    

  $("[type=tel]").mask("+7(000) 000-00-00", {
    placeholder: "+7 (___) ___-__-__"
  });

  //  yandex 
  ymaps.ready(function() {
    var myMap = new ymaps.Map(
        "map",
        {
          center: [47.244729, 39.723187],
          zoom: 13
        },
        {
          searchControlProvider: "yandex#search"
        }
      ),
      //   .
      MyIconContentLayout = ymaps.templateLayoutFactory.createClass(
        '<div style="color: #FFFFFF; width: 100%; height: 100%; font-weight: bold;">$[properties.iconContent]</div>'
      ),
      myPlacemark = new ymaps.Placemark(
        myMap.getCenter(),
        {
          hintContent: "",
          balloonContent: "Lesson-27 |   "
        },
        {
          // .
          //     .
          iconLayout: "default#image",
          //    .
          iconImageHref: "img/location.png",
          //  .
          iconImageSize: [32, 32],
          //      
          //  "" ( ).
          iconImageOffset: [-5, -38]
        }
      );

    myMap.behaviors.disable("scrollZoom");
    myMap.geoObjects.add(myPlacemark);
  });
});
